<template>
    <div class="newsViewer" :config="config" :style="{width: displayWidth, 'min-height': displayHeight}">
        <h3 v-if="getTrans('history_caption')" v-html="getTrans('history_caption', {'%game%': gameName})"></h3>
        <header v-if="displaySwitchs && games" class="switcher">
            <div class="form-inline">
                <div class="form-group">
                    <label for="game_filter">{{ getTrans('game_filter') }}</label>
                    <select name="game_filter" class="form-control bs-select" v-model="currentGame" @change="refreshList()">
                        <option :value="null">{{ getTrans('news_general') }}</option>
                        <option v-for="game in orderBy(Object.values(games), 'name')" :value="game.id" :key="game.id">{{ game.name }}</option>
                    </select>
                </div>
            </div>
        </header>
        <div class="fcnews">
            <div v-if="currentNews" class="news_display">
                <panel :config="{}">
                    <template slot="header">
                        {{ currentNews.title }}<span style="float:right;">{{ $moment.unix(currentNews.published_date).format('LLLL') }}</span>
                    </template>
                    <template slot="body">
                        <header><span v-if="currentNews.channels != null" class="small">{{ getTrans('store_targeting_news') }}<template v-for="ch in currentNews.channels">&nbsp;<font-awesome-icon :icon="['fab', channelIconFor(ch)]" v-tooltip="channelNameFor(ch)" :key="ch"/></template></span>
                        </header>
                        <article class="news_content" v-html="displayMD(currentNews.content)"></article>
                    </template>
                </panel>
            </div>
            <div class="sidelist">
                <panel v-if="news != null" :config="{height: (this.$mq === 'xxs' ? '120px' : '100%') }">
                    <template slot="body">
                        <div v-if="itemCount > 0">
                            <pagination @navigate="navigate" :current-page="currentPage" :page-count="pageCount" :lang="lang" :size="$mq | mq({ xxs: 1, md: 9, xl: 15 })"></pagination>
                            <ul class="news_list">
                                <li v-for="n in news" :key="n.id">
                                    <a @click.prevent.stop="readNews(n.id, n.channels)" :ref="'news-' + n.id">{{ n.title }} - {{ $moment.unix(n.published_date).format('L H:M') }} <span v-if="n.channels != null" v-tooltip="getTrans('store_targeting_news') + ' ' + channelNamesFor(n.channels)"><template v-for="ch in n.channels">- <font-awesome-icon :icon="['fab', channelIconFor(ch)]" :key="ch"/> </template></span></a>
                                </li>
                            </ul>
                        </div>
                        <div v-else style="text-align:center;">
                            <div class="alert alert-info">
                                {{ getTrans('no_news') }}
                            </div>
                        </div>
                    </template>
                </panel>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import HTTP from '@/httpapi';
import Games from '@api/games';
import News from '@api/news';
import Pagination from '@/components/Pagination';
import GenericMixin from '@mixins/generic';
import MarkdownIt from 'markdown-it';

const channelIcons = {amazonestore: 'amazon', googleplay: 'google-play', steam: 'steam', appstore: 'apple', macstore: 'app-store', humblebundle: '', nookstore: ''};
const channelNames = {amazonestore: 'Kindle', googleplay: 'Google Play Store', steam: 'Steam', appstore: 'iOS AppStore', macstore: 'MacOS AppStore', humblebundle: 'Humble Bundle Store', nookstore: 'NookStore'};

const mdIt = new MarkdownIt('default', {typographer: true});
mdIt.enable(['smartquotes']);
mdIt.use(require('markdown-it-checkbox'), {
    divWrap: true
});
mdIt.use(require('markdown-it-implicit-figures'), {
    figcaption: true
});

export default {
    name: 'news-index',

    mixins: [GenericMixin],

    components: {
        pagination: Pagination
    },

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '400px');
        },

        pageCount () {
            return Math.ceil(this.itemCount / this.itemsPerPage);
        },

        displaySwitchs () {
            return (this.config.nosw === undefined || !this.config.nosw);
        },

        gameName () {
            return this.config.newsParams.game_name;
        },

        newsPosition () {
            if (this.config.newsParams && this.config.newsParams.newsPos) {
                return this.config.newsParams.newsPos;
            }

            return null;
        },

        getSpecificNews () {
            if (this.config.newsParams && this.config.newsParams.nid) {
                return this.config.newsParams.nid;
            }

            return null;
        },

        getSpecificNewsChannel () {
            if (this.config.newsParams && this.config.newsParams.channels) {
                return this.config.newsParams.channels;
            }

            return null;
        },

        isDebug () {
            if (this.config.newsParams && this.config.newsParams.dbg && this.getSpecificNews) {
                return true;
            }

            return false;
        }
    },

    methods: {
        init () {
            const that = this;
            axios.all([
                Games.all()
            ]).then(axios.spread(function (games) {
                that.games = games.data.games;
                that.hasGame();
                that.oldGame = that.currentGame;
                that.refreshList();
            }));
        },

        hasGame () {
            if (this.currentGame !== null && !(this.games[this.currentGame])) {
                this.currentGame = null;

                return false;
            }

            return true;
        },

        refreshList () {
            if (this.hasGame() || this.oldGame !== this.currentGame) {
                if (this.getSpecificNews && this.newsPosition) {
                    let requestedPage = Math.max(Math.floor(this.newsPosition / this.itemsPerPage), 1);
                    this.navigate(requestedPage);
                    this.readNews(this.getSpecificNews, this.getSpecificNewsChannel);
                    delete this.config.newsParams.nid;
                    delete this.config.newsParams.newsPos;
                    delete this.config.newsParams.channels;
                } else {
                    this.navigate(this.currentPage);
                    this.hideNews();
                }

                this.oldGame = this.currentGame;
            }
        },

        navigate (requestedPage) {
            const that = this;
            News.all(this.currentGame, this.lang, requestedPage, this.itemsPerPage).then((result) => {
                that.news = result.data.news;
                that.itemCount = result.data.total;

                // Now we check if the requested page is reachable. If it is,
                // then fine, otherwise we go back to the closest possible page
                // (usually the last one).
                const currentPage = Math.max(Math.min(requestedPage, that.pageCount), 1);
                if (currentPage === requestedPage) {
                    that.currentPage = requestedPage;
                } else {
                    that.navigate(currentPage);
                }
            });
        },

        channelIconFor (channel) {
            return channelIcons[channel];
        },

        channelNameFor (channel) {
            return channelNames[channel];
        },

        channelNamesFor (channels) {
            return channels.map(this.channelNameFor).join(', ');
        },

        readNews (newsId, channels) {
            const that = this;
            let channel = ((channels ? channels[0] : null) || 'googleplay');
            axios.all([
                News.get(this.currentGame || '', channel, this.lang, newsId, this.isDebug)
            ]).then(axios.spread((result) => {
                that.currentNews = result.data;
            }));
        },

        displayMD (content) {
            let cth = mdIt.render(content);

            // Table style
            cth = cth.replace(/<table>/g, '<div class="table-responsive"><table class="table table-striped">').replace(/<\/table>/g, '</table></div>');

            // Checkbox not checkable
            cth = cth.replace(/checked="true"><label for="checkbox/g, 'checked="true"><i class="far fa-check-square"></i><label for="checkbox').replace(/"><label for="checkbox/g, '"><i class="far fa-square"></i><label for="checkbox');

            // External link target _blank
            cth = cth.replace(/<a href="http/g, '<a target="_blank" href="http');
            // Internal News link
            cth = cth.replace(/<a href="#(\d+)"/g, '<a href="' + this.urlFor('news_by_id', {id: '$1'}) + '"');

            return cth;
        },

        hideNews () {
            this.currentNews = null;
        }
    },

    watch: {
        // Page URL structure: /games/news(/:page)(?game=...)
        currentPage (page, oldPage) {
            // Sync the last part of the URL with the currently displayed page
            const elements = window.location.pathname.split('/');

            if (elements[elements.length - 1] === '' + oldPage) {
                elements.pop();
            }
            elements.push(page);

            window.history.replaceState({}, '', elements.join('/') + window.location.search);
        },

        currentGame (game, oldGame) {
            const elements = window.location.pathname.split('/');
            let queries = '';
            if (game) {
                queries = '?game=' + game;
            }

            window.history.pushState({}, '', elements.join('/') + queries);
            this.currentPage = 1;
        }
    },

    mounted () {
        HTTP.ifInitialized(this.init);
    },

    data () {
        return {
            games: null,
            oldGame: null,
            currentGame: (this.config.newsParams && this.config.newsParams.game ? this.config.newsParams.game : null),
            news: null,
            itemsPerPage: 20,
            currentPage: (this.config.page ? this.config.page : 1),
            itemCount: 0,
            currentNews: null
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/table.scss';
@import '~sass/text.scss';

.newsViewer /deep/ {
    h3 {
        display: block;
        text-align: center;
        width: 100%;
    }
    .switcher {
        max-height: 44px;
    }

    .fcnews {
        display: flex;
        flex-flow: row wrap;

        & > * {
            padding: 5px;
        }

        .sidelist {
            flex: 1 0;
            order: 1;

            .news_list {
                list-style: none;

                a {
                    color: map-get($asnet-colors, light-purple);
                    cursor: pointer;
                }
            }
        }

        .news_display {
            flex: 2 0px;
            order: 2;

            .panel-body {
                padding-top: 0;

                header {
                    color: gray;
                    font-size: .85em;
                    margin-bottom: 5px;

                    span {
                        border-bottom: solid 1px lightgray;
                        margin-bottom: 5px;
                    }
                }

                article {
                    a {
                        color: map-get($asnet-colors, light-purple);
                    }

                    figure {
                        margin: auto;
                        margin-bottom: 10px;
                        text-align: center;

                        figcaption {
                            color: gray;
                            font-size: .75em;
                        }
                    }

                    h3, h4 {
                        color: #b3b3b3;
                    }

                    table {
                        border-spacing: 2px;
                    }

                    .checkbox {
                        cursor: default;

                        input[type="checkbox"] {
                            display: none;
                        }

                        label {
                            cursor: default;
                            padding-left: 5px;
                        }
                    }
                }
            }
        }
    }
}
</style>



