import HTTP from '@/httpapi';
import Paginate from '@lib/paginate';
import { cacheBasic, cacheReferentiel } from '@lib/cache';

const News = {
    all (game, lang, page, limit) {
        return Paginate(HTTP.convert('/v1/news/all/' + lang, 'news'), page, { limit: limit, game: game, strict: 1 }, { cache: cacheReferentiel });
    },

    get (game, channel, lang, newsId, debug) {
        return HTTP.http.get(HTTP.convert('/v1/news/' + channel + '/' + lang + '/' + newsId + '?game=' + game + (debug ? '&dbg=1' : '')), { cache: cacheBasic });
    }
};

export default News;



